<template>
  <div class="hold">
    <j-header title="持 仓" :go="false" @goback="goback"></j-header>
    <main>
      <div :show="list.length > 0" class="list" v-for="obj in list" :key="obj.createtime">
        <nav>
          <h3>{{ obj.cname }}</h3>
          <span :style="obj.state_name === '买涨' ? 'background: #ea4141' : 'background: #2ba018'">{{ obj.state_name }}</span>
          <span>(￥{{ obj.orderamount }})</span>
          <span>{{ obj.time }}秒</span>
        </nav>
        <h2>{{ obj.startprice }}</h2>
        <p>{{ obj.createtime }}</p>
        <j-progress :value="progress(obj)" bg="#5ea8ec" max="100"></j-progress>
      </div>
      <j-nothing
      :url="require('@/assets/hold/nothing.png')"
      :show="list.length === 0"
      title="没有更多数据了~"
      router="homepage"></j-nothing>
    </main>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import JProgress from '@/components/JProgress'
import JNothing from '@/components/JNothing'
let timer

export default {
  name: 'hold',
  components: {
    JHeader,
    JProgress,
    JNothing
  },
  data () {
    return {
      percent: 1,
      list: [],
      timer: ''
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.request({
        url: '/orders',
        method: 'get',
        params: { endstatus: 0 }
      }).then(res => {
        if (res.code === 1) {
          this.list = res.data
          clearInterval(timer)
          timer = setInterval(() => {
            this.init()
          }, 1000)
        } else {
          this.list = []
          clearInterval(timer)
        }
      })
    },
    progress (obj) {
      const percent = (1 - obj.endtime / obj.time) * 100
      return percent
    },
    goback () {
      // console.log(this.$route.params)
      this.$router.push({ name: 'details', params: this.$route.params })
    }
  },
  mounted () {
  },
  beforeDestroy () {
    clearInterval(timer)
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
