<template>
    <div class="progress-box">
      <p>{{ width }}</p>
      <div class="progress">
        <div class="progress-bar" :style="{'background' : bg, 'width': width }"></div>
      </div>
    </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'jprogress',
  components: {
  },
  props: {
    value: {
      type: null,
      default: 30
    },
    max: {
      type: null,
      default: 100
    },
    bg: {
      type: String,
      default: 'linear-gradient(90deg, #3b95f4 0%, #3f68db 100%)'
    }
  },
  data () {
    return {
    }
  },
  computed: {
    width: {
      get () {
        const percent = this.value / this.max * 100
        return parseInt(percent) + '%'
      },
      set (value) {
        // this.$emit('input', value)
      }
    }
  },
  created () {
  },
  methods: {
    goback () {
      this.$router.go(-1)
    },
    handleIcon () {
      this.$router.push({ name: this.iconRouter })
    }
  }
}
</script>

<style scoped lang="scss">
  @import '@/style/mixin.scss';
  .progress-box {
    min-width: 6rem;
    width: 100%;
    p {
      text-align: right;
      @include space(padding-bottom, .1rem);
      font-size: .26rem;
    }
    .progress {
      height: .14rem;
      overflow: hidden;
      background: #cccccc;
      @include br(.1rem);
      .progress-bar {
        @include br(.1rem);
        height: 100%;
      }
    }
  }
</style>
